import React,{ useEffect} from 'react'

//^ IMPORTING THE REQUIRED BOOTSTRAP COMPONENTS
import {Col, Row} from 'react-bootstrap'

//^ Importing required tools for redux states
import {useSelector, useDispatch} from 'react-redux'

//^ Importing the required components
import Product from '../components/Product'
import Loader from '../components/Loader'
import Message from '../components/Message'

//^ Importing the redux actions 
import { listProducts } from '../redux/actions/productActions'
import { useParams } from 'react-router-dom'
import Paginate from '../components/Paginate'
import ProductCarousel from '../components/ProductCarousel'


const HomeScreen = () => {

    const {keyword} = useParams();

    const {pageNumber} = useParams() ||1;

    const dispatch                 = useDispatch();
    const productList              = useSelector(state => state.productList);
    const {loading,products,error,page,pages} = productList;

    useEffect( 
        
        () => { 
          
            
         dispatch(listProducts(keyword,pageNumber)) }, 
        [dispatch,keyword,listProducts,pageNumber]
    )

    return (
    <>
        <ProductCarousel /> 
        <h1> New Arrivals </h1>
        { loading ? <Loader /> : error ? <Message variant='danger'>{error}</Message>:
        ( <>
            <Row>
                {products.map( (product) => (
                    <Col key={product._id} sm={12} md={6} lg={3} xl={3}>
                        <Product product={product} />
                    </Col>            
                ))}
            </Row>
            <br />
            <Paginate pages={pages} page={page} keyword={keyword ? keyword : ''}></Paginate>
            </>
        )}
    </>
  )
}

export default HomeScreen