import React, { useEffect } from 'react'
import { Button, Col, Row, Table } from 'react-bootstrap';

import { useDispatch,useSelector } from 'react-redux'
import { Link, useNavigate, useParams } from 'react-router-dom';
import Loader from '../components/Loader';
import Message from '../components/Message';
import Paginate from '../components/Paginate';
import { createProduct, deleteProduct, listProducts } from '../redux/actions/productActions';
import { deleteUser, listUsers } from '../redux/actions/userActions';
import { PRODUCT_CREATE_RESET } from '../redux/constants/productConstants';

const ProductListScreen = () => {

    const {pageNumber} = useParams() ||1;

    const dispatch = useDispatch();

    const navigate = useNavigate();

    const productList = useSelector(state=> state.productList)
    const {loading,error,products,page,pages} = productList

    const userLogin = useSelector((state) => state.userLogin)
    const { userInfo } = userLogin

    const productDelete = useSelector((state) => state.productDelete)
    const { 
      loading: loadingDelete, error: errorDelete, success: successDelete 
    } = productDelete

    const productCreate = useSelector((state) => state.productCreate)
    const {
      loading: loadingCreate,
      error: errorCreate,
      success: successCreate,
      product: createdProduct,
    } = productCreate

    useEffect(()=>{
      dispatch({type: PRODUCT_CREATE_RESET})

        if(!userInfo || !userInfo.isAdmin) navigate('/') 
        if(successCreate) navigate(`/admin/product/${createdProduct._id}/edit`)
        else dispatch(
          listProducts('',pageNumber)
        )
    }
    ,
    [dispatch,navigate,userInfo,successDelete,successCreate,pageNumber])


    const deleteHandler = (id) => {
      // console.log(id)
        if(window.confirm('Are you sure')) {
          dispatch(deleteProduct(id))
        }
    }

    const createProductHandler = (product) => {
       dispatch(createProduct())
    }

  return (
    <>
        <Row className='align-items-center'>
            <Col>
            <h1>Products</h1>
            </Col>
            <Col className='text-right'>
            <Button className='my-3' onClick={createProductHandler}>
                <i className='fas fa-plus'></i> Create Product
            </Button>
            </Col>
        </Row>

        {loadingDelete && <Loader />}
        {errorDelete && <Message variant='danger'>{errorDelete}</Message>}
        {loadingCreate && <Loader />}
        {errorCreate && <Message variant='danger'>{errorCreate}</Message>}

        {loading ? (
        <Loader />
      ) : error ? (
        <Message variant='danger'>{error}</Message>
      ) : (
        <>
        <Table   bordered hover responsive className='table-sm'>
          <thead>
            <tr>
              <th>ID</th>
              <th>NAME</th>
              <th>PRICE</th>
              <th>CATEGORY</th>
              <th>BRAND</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {products.map((product) => (
              <tr key={product._id}>
                <td>{product._id}</td>
                <td>{product.name}</td>
                <td>${product.price}</td>
                <td>{product.category}</td>
                <td>{product.brand}</td>
                <td>
                  <Link to={`/admin/product/${product._id}/edit`}>
                    <Button variant='light' className='btn-sm'>
                      <i className='fas fa-edit'></i>
                    </Button>
                  </Link>
                  <Button
                    variant='danger'
                    className='btn-sm'
                    onClick={() => deleteHandler(product._id)}
                  >
                    <i className='fas fa-trash'></i>
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table> <br />
        <Paginate pages={pages} page={page} isAdmin={true}/>

        </>
      )}
    </>
  )
}

export default ProductListScreen