import axios from "axios"
import { LIST_MY_ORDERS_RESET } from "../constants/orderConstants"
import { USER_DELETE_FAIL, USER_DELETE_REQUEST, USER_DELETE_SUCCESS, USER_DETAILS_FAIL, USER_DETAILS_REQUEST, USER_DETAILS_RESET, USER_DETAILS_SUCCESS, USER_LIST_FAIL, USER_LIST_REQUEST, USER_LIST_SUCCESS, USER_LOGIN_FAIL, USER_LOGIN_REQUEST, USER_LOGIN_SUCCESS, USER_LOGOUT, USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS, USER_UPDATE_FAIL, USER_UPDATE_PROFILE_FAIL, USER_UPDATE_PROFILE_REQUEST, USER_UPDATE_PROFILE_SUCCESS, USER_UPDATE_REQUEST, USER_UPDATE_SUCCESS } from "../constants/userConstants"


export const login = (email,password) => async(dispatch) => {
    try {
        
        dispatch({type: USER_LOGIN_REQUEST})

        const config = {
            headers: {
                'Content-Type':'application/json'
            }
        }

        const {data} = await axios.post('http://localhost:4000/api/users/login',{email,password},config)
        
        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data
        })

        localStorage.setItem('userInfo',JSON.stringify(data))


    } catch (error) {
        console.log(error.response.data)
        dispatch({
            type: USER_LOGIN_FAIL,
                payload: 
                error.response && error.response.data.message     
                ? error.response.data.message
                : error.message
            })
        }
}


export const logout = () => (dispatch) =>{
    localStorage.removeItem('userInfo')
    dispatch({type:USER_LOGOUT})
    dispatch({type:USER_DETAILS_RESET})
    dispatch({type:LIST_MY_ORDERS_RESET})
}


export const register = (name, email,password) => async(dispatch) => {
    try {
        
        dispatch({type: USER_REGISTER_REQUEST})

        const config = {
            headers: {
                'Content-Type':'application/json'
            }
        }

        const {data} = await axios.post('http://localhost:4000/api/users/signup',{name,email,password},config)
        
        dispatch({
            type: USER_REGISTER_SUCCESS,
            payload: data
        })

        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data
        })

        localStorage.setItem('userInfo',JSON.stringify(data))


    } catch (error) {
        console.log(error)
        dispatch({
            type: USER_REGISTER_FAIL,
            payload: error
            })
        }
}


export const getUserDetails = (id) => async(dispatch,getState) => {
    try {
        dispatch({type: USER_DETAILS_REQUEST})
        
        const {  userLogin: { userInfo } } = getState()
        const config = { headers: { Authorization: `Bearer ${userInfo.token}` } }
        const { data } = await axios.get(`http://localhost:4000/api/users/${id}`, config)
      
        dispatch({
            type: USER_DETAILS_SUCCESS,
            payload: data
        })

    } catch (error) {
        dispatch({
            type: USER_DETAILS_FAIL,
            payload: error
        })
    }
}


